<script>
import times from "../../components/tableTime";
import layouts from "../../components/layouts";
import tablemap from "../../components/table_map";
import titlebox from "../../components/title";
export default {
    data() {
        return {
            time: "2019-06-14 10:48:53"
        };
    },
    components: {
        times,
        layouts,
        tablemap,
        titlebox
    },
    created() {},
    mounted() {
        this.getMapData();
    },
    methods: {
        getMapData() {
            let vm = this;
            vm.setMap();
        },
        /**
         * 初始化地图
         */
        setMap(data) {
            let point = new BMap.Point(116.404, 39.915);
            //初始化地图

            let map = new BMap.Map("boxMap");

            let vm = this;
            vm.mapObj = map;
            var overView = new BMap.OverviewMapControl();
            var overViewOpen = new BMap.OverviewMapControl({
                isOpen: true,
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT
            });
            map.setMinZoom(3); //最小zoom缩放级别
            map.setMaxZoom(14); //最大zoom缩放级别
            // map.disableScrollWheelZoom(); //关闭鼠标滚轮缩放，自定义滚轮事件，调用map.zoomIn()与map.zoomOut()来控制缩放，避免直接使用而导致一次放大或缩小几级导致数据加载问题
            // vm.bindZoomSwithListener(map); //初始化zoom缩放监听事件
            map.enableScrollWheelZoom();
            // vm.bindProvinceAndCitySwitchListener(map); //初始化map拖动监听事件
            map.addControl(overView); //添加默认缩略地图控件
            map.addControl(overViewOpen);
            map.centerAndZoom(point, 11);
        }
    }
};
</script>

<style scoped lang='scss'>
