    margin-bottom: 10px;
    padding: 0 12px;
    box-sizing: border-box;
}
.page_w {
    width: 90%;
     height: calc(100% - 174px);
    margin-left: 10px;
    background: #fff;
}
.wrap {
    padding: 12px;
    height: 100% ;
    box-sizing: border-box;
}
.map_wrap {
    margin-top: 14px;
    width: 100%;
    height: calc(100% - 76px);
    overflow: hidden;
    position: relative;
    z-index: 0;
    background-color: rgb(243, 241, 236);
    color: rgb(0, 0, 0);
    text-align: left;
}
</style>



